<template>
  <div id="MvDetail">
    <!-- 背景遮罩层 -->
    <div class="bgmask"></div>
    <!-- 背景图 -->
    <div class="bg" :style="{'background-image': 'url('+cover+')','background-color': 'rgb(255,255,255)'}"></div>
    <!-- 视频容器 -->
    <div class="video-container">
      <div class="music-container">
        <video :src="mvUrl.url" controls autoplay="autoplay"></video>
          <div class="video-info">
            <div class="name">{{mvDetail.name}} - {{mvDetail.artistName}}</div>
            <div class="count">播放量：{{mvDetail.playCount}}</div>
          </div>
      </div>
    </div>
    <!-- 主体内容 -->
    <div class="music-container">
      <!-- 大家都爱看 -->
      <div class="similarMv">
        <h3>大家都爱看</h3>
        <div class="mv-content">
          <mv-item
            class="item"
            v-for="(item, index) in similarMvList"
            :key="index"
            :mvId="item.id"
            :cover="item.cover"
            :name="item.name"
            :artistName="item.artistName"
            :artists="item.artists"
            :playCount="item.playCount"
          /> 
        </div>
      </div>

      <!-- MV 简介 -->
      <div class="mv-info">
        <div class="info-header">
          <h3>视频简介</h3>
          <div class="singer">
            <span
              v-for="(item, index) in mvDetail.artists"
              :key="index"
            >{{item.name}} </span>
              <span>发行时间：{{mvDetail.publishTime}}</span>
          </div>
        </div>
        <p class="desc">{{mvDetail.desc}}</p>
      </div>

      <!-- 评论框 -->
      <comment-input :count="mvDetail.commentCount"></comment-input>

      <!-- 评论 -->
      <comment
        class="item"
        v-for="(item, index) in commentList"
        :key="index"
        :avatarUrl="item.user.avatarUrl"
        :nickname="item.user.nickname"
        :content="item.content"
        :time="item.time"
      />

    </div>
  </div>
</template>
<script>
// 工具包
import {
  req_mv_url,
  req_similar_mv,
  req_mv_detail,
} from "../network/mv/index.js";
import { req_mv_comment } from "../network/comment/index.js";
import tools from "../tools/formatData.js";

// 组件包
import commentInput from "../components/commentInput";
import comment from "../components/comment";
import mvItem from "../components/item/mvItem";
export default {
  name: "Mv",
  components: {
    commentInput,
    comment,
    mvItem,
  },
  data() {
    return {
      // 滚轮对象
      domObj: [],
      // MV 视频 id
      id: this.$route.query.id,
      // 音频文件地址
      mvUrl: {},
      // MV 信息
      mvDetail: {},
      // Mv 图像（背景图）
      cover: "",

      // 相似 MV 地址
      similarMvList: [],
     
      // 评论信息
      commentList: [],
      // 当前页数
      page: 0,
      // 页数大小
      commentNum: 20,
    };
  },
  watch: {
    // 播放量可视化
    mvDetail: function() {
      let value = tools._tranNumber(this.mvDetail.playCount, 2);
      this.mvDetail.playCount = value;
    },
    similarMvList: function() {
      this.similarMvList.forEach((e, index, arr) => {
        let value = tools._tranNumber(e.playCount, 2);
        arr[index].playCount = value;
      });
    },
    // 毫秒转秒
    commentList: function() {
      this.commentList.forEach((e, index, arr) => {
        let value = tools._tranDate(e.time);
        arr[index].time = value;
      });
    },
  },
  created() {
    this.getMvUrl();
    this.getsimilarMv();
    this.getMvDetail();
    this.getMvComment();
  },
  mounted() {
    this.init();
  },
  methods: {
    // 页面环境初始化
    init() {
      document.documentElement.scrollTop = 0;
      this.domObj = window;
      this.domObj.onscroll = () => {
        // 滚动视口高度(也就是当前元素的真实高度)
        let scrollHeight =
          document.documentElement.scrollHeight || document.body.scrollHeight;
        // 可见区域高度
        let clientHeight =
          this.domObj.innerHeight ||
          document.documentElement.clientHeight ||
          document.body.clientHeight;
        // 滚动条顶部到浏览器顶部高度
        let scrollTop =
          this.domObj.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;
        if (Math.floor(clientHeight) + Math.floor(scrollTop) == scrollHeight) {
          // 再次获取新 MV 视频
          this.getMvComment();
        }
      };
    },
    // 获取音频地址
    getMvUrl() {
      req_mv_url(this, {
         id: this.id,
      }).then(res => {
        this.mvUrl = res.data;
      }).catch(error => {
        console.log("获取音频地址可能出现错误", error)
      });
    },
    // 获取相似 mv
    getsimilarMv() {
      req_similar_mv(this, {
          mvid: this.id,
      }).then(res => {
        // 截取前四条
        this.similarMvList = res.mvs.slice(0, 4);
      }).catch(error => {
        console.log("获取相似 MV 可能出现错误", error);
      });
    },
    // 获取 Mv 信息
    getMvDetail() {
      req_mv_detail(this, {
        mvid: this.id,
      }).then(res => {
        this.mvDetail = res.data;
        this.cover = this.mvDetail.cover;
      }).catch(error => {
        console.log("获取 MV 信息可能出现错误",error);
      });
    },
    // 获取 mv 评论
    getMvComment() {
      req_mv_comment(this, {
         id: this.id,
         limit: this.commentNum,
         offset: this.page * this.commentNum,
      }).then(res => {
        // 再拼接到数组后面
        this.commentList = this.commentList.concat(res.comments);
        this.page++;
      }).catch(error => {
        console.log("获取 MV 评论可能出现错误", error);
      });
    },
    // MV 详情页
    goMv(id) {
      this.$router.push({
        path: `/mvdetail/${new Date().getTime()}`,
        query: { id },
      });
    },
  },
  beforeDestroy() {
    // 移除监听事件（触底加载评论事件）
    this.domObj.onscroll = null;
  },
};
</script>
<style lang="scss" >
@import "../assets/scss/music.scss";
#MvDetail {
  position: relative;
  z-index: 3;
  .bg {
    position: absolute;
    @include bgmask();
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
  }
  .bgmask {
    @include position();
    background-color: rgba(0, 0, 0, 0.25);
    z-index: -2;
  }
  .video-container {
    margin-top: 20px;
    z-index: 100;
    .music-container {
      video {
        width: 1220px;
        height: 670px;
        display: block;
        margin: 0px auto;
      }
      .video-info {
        border: 1px solid rgba(0, 0, 0, 1);
        padding: 10px;
        border-radius: 12px;
        @include flex(flex-start);
        margin-top: 20px;
        .name {
          @include font(30px, 40px, $colorC);
        }
        .count {
          margin-left: 20px;
          @include font(16px, 40px, $colorC);
        }
      }
    }
  }
  .music-container {
    z-index: 100;
    .similarMv {
      margin-top: 88px;
      h3 {
        margin-top: 20px;
        @include font(28px, 30px, $colorC);
        font-weight: normal;
      }
      .mv-content {
        margin-top: 20px;
        height: 200px;
        @include flex();
        flex-wrap: wrap;
      }
    }

    .mv-info {
      margin-top: 60px;
      margin-bottom: 40px;
      .info-header {
        h3 {
          @include font(30px, 40px, $colorC);
          padding-left: 6px;
        }
        .singer {
          margin-top: 12px;
          border-bottom: 1px solid rgba($color: #000000, $alpha: 0.5);
          padding-bottom: 12px;
          span {
            display: inline-block;
            padding: 0px 10px;
            @include font(18px, 20px, $colorC);
          }
        }
      }
      .desc {
        margin-top: 12px;
        @include font(16px, 30px, $colorB);
        @include fontDHidden(4);
        height: 120px;
        text-align: justify;
      }
    }
  }
}
</style>
